as y coloidales tienen poca conductividad.")])])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-11"},[_c('h5',[_vm._v("pH ")]),_c('p',[_vm._v("El pH es una medida de la concentración de iones de hidrógeno en el agua. Aguas fuera del rango normal de 6 a 9 pueden ser dañinas para la vida acuática. Estos niveles de pH pueden causar perturbaciones celulares y la eventual destrucción de la flora y fauna acuática. En el campo de abastecimiento de agua el pH tiene importancia en la coagulación química, desinfección, ablandamiento del agua y control de corrosión.")])])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-11"},[_c('h5',[_vm._v("Temperatura")]),_c('p',[_vm._v("La temperatura juega un papel muy importante en la solubilidad de los gases, en la disolución de las sales y por lo tanto en la conductividad eléctrica, en la determinación de pH, en el conocimiento del origen de agua y de las eventuales mezclas, etc. Las descargas de agua a altas temperaturas pueden causar daños a la flora y fauna de las aguas receptoras al interferir con la reproducción de las especies, incrementar el crecimiento de bacterias y otros organismos, acelerar las reacciones químicas, reducir los niveles de oxígeno y acelerar la eutrofización.")])])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-11"},[_c('h5',[_vm._v("Medición de temperatura ")]),_c('p',[_vm._v("La medición de temperatura en muestras ambientales debe ser una labor realizada "),_c('em',[_vm._v("in situ")]),_vm._v(", y no aplica por ello los procedimientos de almacenamiento y preservación. El mejor método para la lectura de este parámetro es introducir directamente los equipos de medición (termómetro o sonda) en el cuerpo de agua.")])])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-11"},[_c('h5',[_vm._v("Turbidez ")]),_c('p',[_vm._v("La turbidez del agua es provocada por la materia